<template>
	<div class="user_information">
    <van-nav-bar
      title="个人资料"
      left-text="返回"
      right-text=""
      left-arrow
      @click-left="goBack"
    />
		<van-cell-group>
      <van-cell title="头像" class="cell_middle">
        <van-uploader :after-read="onRead" accept="image/*" multiple>
          <img class="head-img" :src="portrait" ref="portrait_img" style="width:50px; height:50px;"/>
        </van-uploader>
      </van-cell>
      <van-cell title="淘宝ID" :value="taobaoUser" />
			<van-cell title="姓名" :value="name" />
      <van-cell :value="bank" title="开户银行" />
      <van-cell :value="bank_number" title="银行卡号" />
      <van-cell title="手机号" :value="account"></van-cell>
			<van-cell title="密码设置" to="/user/information/setPassword" isLink />
		</van-cell-group>

    <div style="padding:10px;">
      <van-button size="large" type="danger" @click="logout">退出登录</van-button>
    </div>
	</div>
</template>

<script>
	import { Uploader, Picker, Toast, Dialog } from 'vant';
	import {
    USER_UPDATE_INFO,USER_LOGOUT
  } from '@/api/user';

	export default {

		data(){
			return {
        "defaultImg": require(`../../../assets/images/avatar_default.png`),
        "portrait": "",
        "taobaoUser":"",
        "name": "",
        "address": "",
        "sex": "",
        "age": "",
        "bank": "",
        "qq": "",
        "wechat": "",
        "account": "",
        "bank_number": "",
        "provide": "",
        "city": "",
        "county": "",
        "area_code": "",
			}
		},
		computed: {
		},
		created(){
      this.getUserInfo();
		},

		methods: {
      onRead(file) {
        const files = file.file;
        if (files.size > 5 * 1000000) {
          Dialog.alert({
            message: `<p style="text-align:center;">上传文件过大, 请重新选择</p>`
          });
        }
        let formdata = new FormData();
        formdata.append("portrait",files);
        this.$reqPost(USER_UPDATE_INFO, formdata)
          .then((res) => {
            Toast(res.data.msg);
            this.$refs.portrait_img.src = file.content;
            localStorage.portrait = res.data.data.head_pic;
          }).catch((err) => {
          });
      },
			getUserInfo(){
			  let _this = this;
        _this.$user.getUserInfo(function(data) {
          _this.initUser(data);
        });
			},
      initUser(data){
        // console.log(data.portrait)
        if (data.portrait && data.portrait != 'null') {
          this.portrait = data.portrait;
        } else {
          this.portrait = this.defaultImg;
        }
        this.taobaoUser = data.taobaoUser;
        this.name = data.name;
        this.address = data.address;
        this.sex = data.sex;
        this.bank = data.bank;
        this.qq = data.qq;
        this.wechat = data.wechat;
        this.account = data.account;
        this.bank_number = data.bank_number;
      },
      logout(){
        this.$reqGet(USER_LOGOUT).then((res) => {
           let result = res.data;
           console.dir(result);
           if(result.code!=0){
               Toast.fail(result.msg);
               return;
           }
          this.$util.removeLocalStorage("portrait",
            "name",
            "address",
            "sex",
            "age",
            "bank",
            "qq",
            "wechat",
            "account",
            "card_no",
            "provide",
            "city",
            "county",
            "area_code",
            "token",
            "account",
            "auth");
          this.$router.push({name: 'login'});
        }).catch((err) => {

        });

      },
      //返回
      goBack() {
        this.$router.push({path: '/user'});
      }
		},

		components: {
			[Uploader.name]: Uploader,
			[Picker.name]: Picker,
      [Toast.name]: Toast
		}
	}
</script>


<style lang="scss" scoped>

	.user_information{
		.user_avatar_upload{
			position: relative;
			width: 50px;
			height: 50px;
			border: 1px solid $border-color;
			img{
				max-width: 100%;
				max-height: 100%;
			}
			i{
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				font-size: 20px;
				color: $border-color;
			}
		}
	}
</style>



